import { Component, OnInit, ViewChild } from '@angular/core';
import { IonSlides, ModalController } from '@ionic/angular';
import { Router } from '@angular/router';
import { ModalTipoApostaComponent } from '../modal-components/modal-tipo-aposta/modal-tipo-aposta-component';
import { Storage } from '@ionic/storage';
import { ModalPuleComponent } from '../modal-components/modal-pule/modal-pule-component';

@Component({
    selector: 'app-home',
    templateUrl: './home.page.html',
    styleUrls: ['./home.page.scss'],
})

export class HomePage implements OnInit {

    usuario: any;
    showBanner = false;

    jogoInfo = {
        data: '17/12/2020',
        valor: 140,
        tipo: 'Grupo',
        animal: '16 - Leão',
        premio: 'Cabeça',
        numeros: ['63', '23', '56', '77'],
        sorteios: ['11', '17'],
    };

    isCard = null;

    constructor(private router: Router,
                private storage: Storage,
                private modalCtrl: ModalController) {
    }

    async ngOnInit() {
        console.log('ngOnInit - > HomePage');
        await this.checkAuthenticate();
    }

    async ionViewWillEnter() {
        await this.checkAuthenticate();
        setTimeout(() => {
            this.showBanner = !this.usuario;
        }, 1000);
    }

    async checkAuthenticate() {
        this.storage.get('is-login').then((user) => {
            if (user) {
                // this.showBanner = false;
                this.isCard = 'resultado';

                this.usuario = {
                    nome: 'Roberto Perreira Lima',
                    saldo: '5.930',
                };
            } else {
                this.isCard = 'jogo';
                // this.showBanner = true;
            }
        });
    }

    async onModalBetType() {
        const modal = await this.modalCtrl.create({
                component: ModalTipoApostaComponent,
                keyboardClose: true,
                cssClass: 'modal-fab-bottom modal-bet-type',
                componentProps: {}
            }
        );

        modal.onWillDismiss().then((type: any) => {
            if (type && type.data) {
                this.openGame(type.data);
            }
        });
        return await modal.present();
    }

    async openModalPule() {
        const modal = await this.modalCtrl.create({
                component: ModalPuleComponent,
                keyboardClose: true,
                cssClass: 'modal-fab-bottom',
                componentProps: {}
            }
        );

        // modal.onWillDismiss().then((type: any) => {});
        return await modal.present();
    }

    openCard(card) {
        this.isCard = card;
    }

    openGame(type) {
        this.router.navigate(['jogo'], {
            state: {data: type}
        });
    }

}
